.user {
    border: 2px solid black;
    height: 150px;
    border-radius: 5px;
    display: flex;
    margin-bottom: 15px;
    width: 70%;
}

.user_left {
    display: flex;
    flex-direction: column;
    width: 10%;
    min-height: 100%;
    justify-content: center;
    padding: 0 10px 0 10px;
    align-items: center;

}

.user_left button {
    padding: 5px 10px;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.5s;

}

.user_left button:hover {
    background-color: #262626;
    border: 2px solid grey;
    color: white;
}

.user_left img {
    width: 100px;
    height: 100px;
}

.user_right {
    width: 90%;
}

.user_right_top {
    display: flex;
    justify-content: space-around;
}

.user_right_name {}

.user_right_city {}

.user_right_bottom {
    display: flex;
    justify-content: space-around;
}

.user_right_status {}

.user_right_zipcode {}